//

module.exports = {
    getSafeRandomNum,
    getRandomNum,
}

//  min-max 
function getSafeRandomNum(min, max) {
    var seed = (new Date()).getTime();
    seed = (seed * 9301 + 49297) % 233280;
    var rand = seed / (233280.0);
    var range = max - min;
    return Math.floor(rand * (range + 1) + min);
}

//  min-max 
function getRandomNum(min, max) {
    var range = max - min;
    var rand = Math.random();
    return Math.floor(rand * (range + 1) + min);
}

