//

const myAxios = require('axios');
const { Toast } = require('vant');

const isDevelop = process.env.NODE_ENV === "development";
const kToastHiddenTime = 1000;

myAxios.defaults.timeout = 15000;

module.exports = {
    get,
    post,
}


// http request 
myAxios.interceptors.request.use(
    (config) => {
        //
        config.data = handleSendData(config.data);
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// http response 
myAxios.interceptors.response.use(
    (response) => {
        return response;
    },
    (error) => {
        // console.log(error);
        // console.log(error.message);
        // console.log(error.response);
        // console.log(error.response.status);
        // 
        if (error.code === 'ECONNABORTED' || error.message === 'Network Error' || error.message.indexOf('timeout') !== -1) {
            Toast('');
            return Promise.reject({
                code: -10000,
                msg: '',
            });
        }
        if (error && error.response.status) {
            switch (error.response.status) {
                case 400:
                    error.message = "Bad Request/(400)";
                    break;
                case 401:
                    error.message = "Unauthorized/,(401)";
                    break;
                case 403:
                    error.message = "Forbidden/(403)";
                    break;
                case 404:
                    error.message = "Not Found/(404)";
                    break;
                case 408:
                    error.message = "Request Timeout/(408)";
                    break;
                case 500:
                    error.message = "Internal Server Error/(500)";
                    break;
                case 501:
                    error.message = "Not Implemented/(501)";
                    break;
                case 502:
                    error.message = "Bad Gateway/(502)";
                    break;
                case 503:
                    error.message = "Service Unavailable/(503)";
                    break;
                case 504:
                    error.message = "Gateway Timeout/(504)";
                    break;
                case 505:
                    error.message = "HTTP Version Not Supported/HTTP(505)";
                    break;
                default:
                    error.message = `(${error.response.status})!`;
            }
        }
        return Promise.reject({
            code: error.response.status,
            msg: error.message,
        });
    }
);

function getToken() {
    let token = "1";
    return token;
}

/*  */
function getHeader() {
    let header = {
        "content-type": "application/x-www-form-urlencoded",
        version: "1.0.0",
        token: getToken(),
    };
    return header;
}

//
function handleSendData(params) {
    if (isDevelop) {
        console.log("===== HttpUtils  =====", params);
    }
    return params;
}

//
function handleReturnData(res) {
    if (isDevelop && res) {
        console.log("===== HttpUtils  =====", res.data);
    }
    return res;
}

/**
 * function: /
 * @isShow /
 * @loadingText 
 */
function showLoading(isShow, loadingText) {
    console.log(isShow);
    if (isShow == false) {
        Toast.clear();
        return;
    }
    if (loadingText) {
        if (loadingText != "" && isShow == true) {
            Toast.loading({
                message: loadingText,
                duration: 0,
                // forbidClick: true,
            });
        }
    }
}

/*  */
const request = (url, method, params, loadingText) => {
    showLoading(true, loadingText);
    return new Promise((resolve, reject) => {
        if (method === "GET") {
            myAxios
                .get(url, {
                    params: params,
                    headers: getHeader(),
                })
                .then((res) => {
                    setTimeout(() => {
                        Toast.clear();
                        resolve(handleReturnData(res.data));
                    }, kToastHiddenTime);
                })
                .catch((error) => {
                    setTimeout(() => {
                        Toast.clear();
                        reject(error);
                    }, kToastHiddenTime);
                });
        }
        if (method === "POST") {
            myAxios
                .post(url, {
                    params: params,
                    headers: getHeader(),
                })
                .then((res) => {
                    setTimeout(() => {
                        Toast.clear();
                        resolve(handleReturnData(res.data));
                    }, kToastHiddenTime);
                })
                .catch((error) => {
                    setTimeout(() => {
                        Toast.clear();
                        reject(error);
                    }, kToastHiddenTime);
                });
        }
    });
};

/* get */
function get(url, params, loadingText) {
    return request(url, "GET", params, loadingText);
}

/* post */
function post(url, params, loadingText) {
    return request(url, "POST", params, loadingText);
}



